
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellFormPreview from 'vux/src/components/cell-form-preview/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'

import axios from 'axios'

export default {
components: {
Group,
Cell,
CellFormPreview,
XButton,
XInput,
axios
},
data () {
return {
list: [],
prices: []
}
},
methods: {
nameFocus: function () {
this.name = ''
},
nameEnter: function () {
this.$refs.priceInput.focus()
},
priceFocus: function () {
this.price = ''
},
totalPriceFocus: function () {
this.total_price = ''
},
priceEnter: function () {
let name = this.name
let price = this.price
this.append(name, price)
},
append: function (name, price) {
this.list.push({
label: name,
value: price
})
this.name = ''
this.price = ''
this.$refs.nameInput.focus()
},
submit: function (totalPrice) {
let params = {
actual_price: parseFloat(totalPrice),
prices: this.list.map(function (x) {
return {
name: x.label,
price: parseFloat(x.value)
}
})
}
console.log(params)
let result = axios.request({
url: '/api/pindan',
method: 'POST',
baseURL: 'http://120.27.17.226',
headers: {'Content-Type': 'application/json'},
data: params
})
.then(response => {
console.log(response)
this.prices = response.data.map(function (x) {
return {
label: x.name,
value: x.price
}
})
})
.catch(function (error) {
alert(error)
})
this.prices = result.map(function (x) {
return {
label: x.name,
value: x.price
}
})
}
}
}



